import React from "react";
import CallToAction from "../components/call-to-action/";

const Resources = () => (
  <div>
    <div className="container">
      <div className="section" style={{ minHeight: "800px" }}>
        <h1>Resources</h1>
      </div>
    </div>
    <CallToAction />
  </div>
);

export default Resources;



